<template lang="pug">
include ../../../mixins/bemto/bemto
transition(name="fade")
  +b.loader
    +e.title {{ title }}
    .lds-roller
      div(v-for="(dot, i) in dots", :key="i")
</template>
<script>
export default {
  props: {
    title: {
      type: String,
      default() {
        return "";
      },
    },
  },
  data: () => ({
    dots: 8,
  }),
};
</script>
